import { phoneReplace } from "./replaceValues";

export const changeInputs = (elements) => {
    elements.forEach(input => {
        input.addEventListener('input', (e)=> {                
            const that = e.target
            if(that.name === "name" && that.value.length > 3) that.parentElement.classList.remove('error')
            if(that.name === "phone" && phoneReplace(that.value).length > 11) that.parentElement.classList.remove('error')
        })
    });
}