import { resetCalculator } from "../calculation-step/calculation-step";
import { changeInputs } from "./components/changeInputs";
import fetchRequest from "./components/fetchRequest";
import { phoneReplace } from "./components/replaceValues";
import { validationInput } from "./components/validationInput";



export default ()=> {
    const form = document.querySelectorAll('body .js-form');    
    form.forEach(item=> {
        item.addEventListener('submit', (e)=> {
            e.preventDefault()
            const target = e.target
            const formInput = target.querySelectorAll( '.js-input')
            const formError = target.querySelectorAll( '.js-form__error')
            const formBtn = target.querySelector('.js-form-btn')
            const formDataEntries = new FormData(target);
            const { name, phone } = Object.fromEntries(formDataEntries.entries());
            
            const dataObject = {}
            for (let [key, value] of formDataEntries) {
                dataObject[key] = value
            }

            const url = target.dataset.action
            const validation = validationInput(name, phone, formInput, formError)
            if(!validation) return changeInputs(formInput)
            
            const dataObjectClone = {
                ...dataObject,
                name: name.trim(),
                phone: phoneReplace(phone)
            }

            formBtn.setAttribute('disabled', true)
            fetchRequest(url,
                dataObjectClone,
                ()=> {
                    formBtn.removeAttribute('disabled')
                    target.reset()
                    Fancybox.show([{ src: "#success", type: "inline" }]);
                    resetCalculator();
                    setTimeout(()=> Fancybox.close(), 3000)
                }
            )
        })
    })
}

