export default ()=> {
    const accordionHead = document.querySelectorAll('.js-accordion__head');
    const accordionBody = document.querySelectorAll('.js-accordion__body');
    const stageHead = document.querySelectorAll('.js-stage-head');
    const stageBody = document.querySelectorAll('.js-stage-body');

    let lastElement = null
    accordionHead.forEach((item, i) => {
        item.addEventListener('click', function(e) {
            const target = this
            if(target === lastElement) return
            accordionHead.forEach(head=> head.classList.remove('active'))
            accordionBody.forEach(body=> body.classList.remove('active'))
            stageHead.forEach(head=> head.classList.remove('active'))
            stageBody.forEach(body=> body.classList.remove('active'))
            target.nextElementSibling.querySelectorAll('.js-stage-head')[0].classList.add('active')
            target.nextElementSibling.querySelectorAll('.js-stage-body')[0].classList.add('active')
            target.classList.add('active')
            target.nextElementSibling.classList.add('active')
            lastElement = target
        })
    })
}