export default ()=> {
    const swiper = new Swiper('.js-team__slider', {
        loop: true,
        slidesPerView: 1.3,
        spaceBetween: 10,
        pagination: {
            el: '.swiper-pagination',
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },

        breakpoints: {
            // when window width is >= 320px
            769: {
              slidesPerView: 5,
            },

        }
    })

    const questions = new Swiper('.js-team__questions', {
        direction: 'vertical',
        loop: true,
        speed: 2000,
        nested: true,
        autoplay: {
            delay: 3000,
        },
    });
}