export default ()=> {

    const Template = (scr, title, list)=> (
    `
        <div class="brand-modal">
            <button class="brand-modal__close js-brand-modal__close">
                <svg>
                    <use xlink:href="./img/sprites/sprite.svg#accent-arrow"></use>
                </svg>
                <span>назад</span>
            </button>
            <div class="brand-modal__inner">
                <div class="brand-modal__item">
                    <div class="brand-modal__logo">
                        <img src="${scr}" alt="Logo" /> 
                    </div>
                </div>
                <div class="brand-modal__item">
                    <div class="brand-modal__content">
                        <h3 class="brand-modal__title">${title}</h3>
                        <div class="brand-modal__center">
                            <h3 class="brand-modal__title">Что делаем</h3>
                            <ul class="brand-modal__list">
                                ${list.map(item => `
                                    <li>
                                        <svg>
                                            <use xlink:href="./img/sprites/sprite.svg#tick"></use>
                                        </svg>
                                        <span>${item}К</span>
                                    </li>
                                `).join(' ')}
                            </ul>
                        </div>
                    </div>
                    <div class="brand-modal__bottom">
                        <a href="#" class="brand-modal__link">
                            <span>кейсы</span>
                            <svg>
                                <use xlink:href="./img/sprites/sprite.svg#accent-arrow"></use>
                            </svg>
                        </a>
                        <a href="#" class="brand-modal__link">
                            <span>отзывы</span>
                            <svg>
                                <use xlink:href="./img/sprites/sprite.svg#accent-arrow"></use>
                            </svg>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    `
    )

    document.addEventListener('click', (e)=> {
        const target = e.target
        if(target.closest('.js-brand')) {
            const scr = target.dataset.src
            const title = target.dataset.title
            const list = target.dataset.list
            Fancybox.show([
                {
                  src: Template(scr, title, list.split(',')),
                  type: "html",
                },
            ], {
                closeButton: null
            });
        }
    })

    document.addEventListener('click', (e)=> {
        const target = e.target
        if(target.closest('.js-brand-modal__close')) Fancybox.close()
    })
}