export default ()=> {

    const swiper = new Swiper('.js-intro-swiper', {
        loop: true,
        effect: 'fade',
        speed: 500, 
        autoplay: {
            delay: 5000,
        },
        pagination: {
            el: '.intro-swiper-pagination',
            clickable: true,
        },

        on: {
            init: function () {
            },
        },
    })
}