import { changeClassActive, changeInputs, changeRadioBtn, changeRegion, customSelectra, radioNextStep, regionAndSiteInput } from "./components";

let count = 0;
let nextStage = [false, false, false, false]
let submitModeBtn = false;
const counterNumb = document.querySelector('.js-counter-numb');
const calculationStepItem = document.querySelectorAll('.js-calculation-step__item');
const calculationStepNext = document.querySelector('.js-calculation-step__next');
const calculationStepPrev = document.querySelector('.js-calculation-step__prev');
const calculationStepError = document.querySelector('.js-calculation-step-error');
const calculationStepRadio = document.querySelectorAll('.js-calculation-step__radio');
const calculationStepInput = () => document.querySelectorAll(".js-calculation-step__input");
const selectRegion = document.querySelector('.js-select-region');
const otherRegion = document.querySelector('.js-form__otherRegion');

export default ()=> {
    customSelectra.init()
    
    const actionStep = (valid) => {
        nextStage[count] = valid
        if(valid) {
            count += 1
            changeClassActive(calculationStepItem, count)
            calculationStepError.classList.remove('error')
            counterNumb.innerText = `0${count + 1}`
        }
    }

    calculationStepNext.addEventListener('click', function() {
        if(submitModeBtn) this.type = "submit"
        if(count < 3) {
            radioNextStep(calculationStepItem, count, actionStep)
            calculationStepError.innerText = "Пожалуйста выберите параметр"
        }
        if(count === 3) {
            regionAndSiteInput(calculationStepInput(), actionStep)
            calculationStepError.innerText = "Пожалуйста заполните поля"
        }
        if(!nextStage[count] && count <= 3) calculationStepError.classList.add('error')
        if(count === 4) {
            this.className = 'btn btn_spec btn_yellow js-form-btn calculation-step__submit js-calculation-step__next'
            this.children[1].innerText = "оставить заявку"
            submitModeBtn = true
        }
    })

    calculationStepPrev.addEventListener('click', ()=> {
        if(count !== 0) count = --count
        counterNumb.innerText = `0${count + 1}`
        calculationStepError.classList.remove('error')
        changeClassActive(calculationStepItem, count)
        submitModeBtn = false
        if(count < 4) {
            calculationStepNext.className = 'calculation-step__next calculation-step__arrow js-calculation-step__next'
            calculationStepNext.children[1].innerText = "далее"
        }
    })

    changeInputs(calculationStepInput(), calculationStepError)

    changeRadioBtn(calculationStepRadio, actionStep)

    changeRegion(selectRegion, otherRegion);
}

export const resetCalculator = () => {
    count = 0;
    nextStage = [false, false, false, false];
    submitModeBtn = false;
    changeClassActive(calculationStepItem, count);
    counterNumb.innerText = `0${count + 1}`;
} 