export default ()=> {
    const resultsSlider = new Swiper('.js-results-slider', {
        slidesPerView: 1,
        spaceBetween: 20,
        pagination: {
          el: '.results-slider-pagination',
        },
      
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        breakpoints: {
          769: {
            slidesPerView: 2,
            spaceBetween: 0,
          },
        }
    });
}