export default ()=> {
    ymaps.ready(init); 
	let newMap;
	
	function init() {

		let place;

		newMap = new ymaps.Map("ymaps", {
            center: [55.783245, 37.668884],
            zoom: 14,
            controls: []
		});

        place = new ymaps.Placemark([55.783245, 37.668884], {
            hintContent: `
                <div class="contacts__map-mark">
                    <img src="./img/content/pop.svg" alt="Baloon"/>
                    <span>Честный digital</span>
                </div>
            `,
        }, {
            iconLayout: 'default#image',
            iconImageHref: './img/content/pin.png',
            iconImageSize: [50, 50],
            // iconImageOffset: [-50, -30]
        })

        newMap.geoObjects.add(place);
        
        const width = window.innerWidth
        if(width < 770) {
            newMap.behaviors.disable('drag');
        }
	};
}