export default () => {
    const phoneMask = Inputmask({"mask": "+ 7 (999) 999 - 99 - 99"})
    phoneMask.mask('.js-phone')

    const modalClose = document.querySelectorAll('.js-modal-close');
    const formInput = document.querySelectorAll( '.js-input')
    const formError = document.querySelectorAll( '.js-form__error')

    modalClose.forEach(item=> {
        item.addEventListener('click', ()=> {
            Fancybox.close()
            formInput.forEach(item=> item.classList.remove('error'))
            formError.forEach(item=> item.classList.remove('error'))
        })
    })

    const formBtn = document.querySelectorAll('.js-form-btn');
    formBtn.forEach(btn=> btn.type= "submit")
}