import { phoneReplace } from "./replaceValues";

export const validateName = (name, minlen) => {
    if (!name) return "Имя обязательно";
    if (name.length < minlen) return `Пожалуйста, введите Имя длиной не менее ${minlen} символов`;
    if (!/(^[a-zA-Z\-]+$)|(^[а-яА-Я\-]+$)/.test(name.trim())) return "Пожалуйста, вводите только буквы";
    return "";
}

export const validatePhone = (phone, minlen) => {
    if (!phone) return "Номер телефона обязательна.";
    const replace = phoneReplace(phone)
    if (replace.length < minlen) return "Пожалуйста, введите корректный номер телефона";
    return "";
}