
export default ()=> {

    const honestClose = document.querySelector('.js-honest__close');

    Fancybox.bind("[data-fancybox]", {
        dragToClose: false,
        closeButton: false,
        on: {
            init: () => {
                const swiper = new Swiper('#honest-slider', {
                    loop: true,
                    speed: 500,
                    slidesPerView: 3,
                    spaceBetween: 24,
                    autoplay: {
                        delay: 5000,
                    },
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    },
                })
            },
        },
    })

    honestClose.addEventListener('click', ()=> Fancybox.close())

    const swiper = new Swiper('#honest-slider', {
        loop: true,
        speed: 500,
        slidesPerView: 1,
        spaceBetween: 24,
        autoplay: {
            delay: 5000,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
    })
}