export default ()=> {
    const stageHead = document.querySelectorAll('.js-stage-head');
    const stageBody = document.querySelectorAll('.js-stage-body');
    
    stageHead.forEach((item, i) => {
        item.addEventListener('click', function() {
            const target = this
            stageHead.forEach(head=> head.classList.remove('active'))
            stageBody.forEach(body=> body.classList.remove('active'))
            target.classList.add('active')
            target.nextElementSibling.classList.add('active')
        })
    })
}