export default ()=> {
    const clientCategoryBtn = document.querySelectorAll('.js-client-category')
    const brandsItem = document.querySelectorAll('.js-brand');
    
    clientCategoryBtn.forEach(item=> {
        item.addEventListener('click', (e)=> {
            clientCategoryBtn.forEach(x=>x.classList.remove('active'))
            const target = e.target
            target.classList.add('active')
            const value = target.dataset.value.toLowerCase();
    
            brandsItem.forEach((category, i)=> {
                const name = category.dataset.category
                if(value === 'all') return category.style.display="flex"
                if(name === value) return category.style.display="flex"
                category.style.display="none"
            })
        })
    })
}