export const radioNextStep = (parentElement, idx, changeState) => parentElement[idx].querySelectorAll('input').forEach(item=> item.checked ? changeState(true) : null)

export const changeRadioBtn = (input, changeState) => input.forEach(radio=> radio.addEventListener('change', ()=> changeState(true)))

export const changeClassActive = (container, idx) => {
    try {
        if(container[idx].classList.contains('active')) return
        container.forEach(item=> item.classList.remove('active'))
        container[idx].classList.add('active')
    } catch {
        console.log("error syntax")
    }
}

export const regionAndSiteInput = (calculationStepInput, actionStep) => {
    let valid = false
    calculationStepInput.forEach(input => !input.value ? valid = false  : valid = true)
    actionStep(valid)
}

export const customSelectra = new Selectra('.js-select-region', {
    search: true,
    langInputPlaceholder: "Регион",
})

export const changeRegion = (selectRegion, otherRegion) => {
    selectRegion.addEventListener('change', (e)=> {
        const value = e.target.value
        const template = `
            <label for="otherRegion" class="calculation-step__label calculation-step__otherRegion">
                <input type="text" name="otherRegion" id="otherRegion" class="form__input js-calculation-step__input" autocomplete="off" placeholder="Введите регион" >
            </label>
        `
        if(value === "Другой")  {
            otherRegion.style.display = "block";
            otherRegion.innerHTML = template
            return
        }
        otherRegion.style.display = "none";
        otherRegion.innerHTML = ""
    })
}

export const changeInputs = (inputs, errorElement) => inputs.forEach(element => element.addEventListener('input', ()=> errorElement.classList.remove('error')));